import delegate from 'delegate-to';
import isEqual from 'lodash/isEqual';
import { goto } from '../libs/util';

export default {
    install(Vue, config) {
        /**
         * 注册自定义指令goto,内部包含了本地开发环境下的跳转方式
         */
        Vue.directive('goto', {
            bind(el, binding) {
                if (typeof binding.value !== 'object') return;
                el.dataset.directiveName = 'goto';
                el.dataset.directiveData = JSON.stringify(binding.value);
            },
            update(el, binding) {
                if (!isEqual(binding.value, binding.oldValue)) {
                    el.dataset.directiveName = 'goto';
                    el.dataset.directiveData = JSON.stringify(binding.value);
                }
            },
        });
        document.body.addEventListener('click', delegate('[data-directive-name="goto"]', (e) => {
            const link = JSON.parse(e.delegateTarget.dataset.directiveData);
            goto(link);
        }), false);
    },
};
