export default {
    computed: {
        g7sHeight() {
            // 说明不在g7s框架里
            if (this.$store.state.page.clientHeight === -1) {
                return {};
            }
            return { height: `${this.$store.state.page.clientHeight - 90}px`, display: 'flex', flexDirection: 'column' };
        },
    },
};
