import isEmpty from 'lodash/isEmpty';

/**
 * 解析postmessage参数
 *
 * @param  {[type]} str [description]
 * @return {[type]}     [description]
 */
function parsePostMessageParams(str) {
    try {
        return JSON.parse(str);
    } catch (e) {
        return {};
    }
}

function Listenner(vm) {
    this.vm = vm;
}

Listenner.prototype.start = function start() {
    window.addEventListener('message', this.onmessage.bind(this), false);
};

Listenner.prototype.onmessage = function onmessage(e) {
    const params = parsePostMessageParams(e.data.toString());
    if (isEmpty(params)) return;

    for (const [key, value] of Object.entries(params)) {
        switch (key) {
            case 'height':
                this.vm.$store.commit('page/update', { clientHeight: value });
                break;
            case 'scroll':
                this.vm.$store.commit('page/update', { scrollTop: value });
                break;
            default:
        }
    }
};

export default Listenner;
