
const config = {
    // menu 对应三种，takeMune，setMenuHidden，toggleMenuHidden
    menu: {
        true: {
            takeMune: '',
        },
        false: {
            setMenuHidden: '',
        },
        hide: {
            toggleMenuHidden: true,
        },
    },
    login: {
        toLogin: true,
    },
    logout: {
        doLogout: true,
    },
    error(errorCode) {
        const code = isNaN(Number(errorCode)) ? '' : Number(errorCode);
        if (code === '') return '';
        return {
            HttpError: code,
        };
    },
    // url, toURL,
    /**
     * {
     *     path: '',
     *     blank: true // 是否新窗口打开
     * }
     */
    url(params) {
        let url = '';
        if (typeof params === 'object') {
            url = params.path || '';
            if (params.blank) {
                url = `${url}&__blank__1`;
            }
        } else if (typeof params === 'string') {
            url = params;
        }
        return url ? {
            toURL: url,
        } : '';
    },
    download: {

    },
    fullScreen: {
        fullscreen: '',
    },
    reload: {
        reload: '',
    },
    refeshAlarm(params) {
        return params;
    },
    breadcrumb(params) {
        const crumbs = {};
        if (Array.isArray(params)) {
            params.forEach((item) => {
                if (item.language) {
                    const { title, path } = item;
                    crumbs[item.language] = {
                        title,
                        path,
                    };
                }
            });
        } else if (typeof params === 'object') {
            const { language = 'zh-cn', title, path } = params;
            crumbs[language] = {
                title,
                path,
            };
        }
        return crumbs;
    },
    check: {
        nohash: ''
    },
    feedBack(params) {
        // const params = true;
        const result = {
            showFeedback: true,
        };
        if (typeof params === 'string') {
            Object.assign(result, {
                content: params,
            });
        } else if (typeof params === 'object') {
            Object.assign(result, params);
        }
        return result;
    },
    contact: {
        contactService: '',
    },
    SPA(params) {
        const result = {
            system: '',
            src: '',
        };
        return Object.assign(result, {
            open: params.blank || false,
        }, params);
    },
    goto(params) {
        return Object.assign({
            url: '',
            system: false, // 菜单复制可以传此参数，通过getSubsystemByUrl 去获取子系统
            blank: false,
            hash: true,
        }, params);
    },
    adaptor: {
        adjustOnce: true, // 重新适配当前页面布局
        getSize: true,
    },
    getSize: {
        getSize: true,
    },
    scroll(params) {
        return {
            pageScrollTo: params,
        };
    },
    export(params) {
        const result = {
            animation: true,
            top: 10,
            left: 10,
            params: null,
            showPanel: true,
        };
        return Object.assign(result, params);
    },
    /**
     * @param {*} params { type: 'success', content: '' };
     */
    notify(params) {
        const config = {
            type: 'info', //
            content: '',
            title: '',
            timeout: 5000,
        };
        return {
            notify: Object.assign(config, params),
        };
    },
};

/**
 * @param {param} 需要切换的功能参数，分别为true，false， hide，
 * @return config
 */
function menu(param) {
    let result = null;
    switch (param) {
        case true: {
            result = Object.assign({}, config.menu.true);
            break;
        }
        case false: {
            result = Object.assign({}, config.menu.false);
            break;
        }
        case 'hide': {
            result = Object.assign({}, config.menu.hide);
        }
    }
    return result ? result : '';
}

let runing = false;

export default class G7s {
    constructor() {
        this.queue = [];
        const no = ['menu', 'error'];
        for (const [key, value] of Object.entries(config)) {
            if (no.indexOf(value) === -1 && !this[key]) {
                this[key] = function () {
                    this.add(value);
                }
            }
        }
    }

    /**
     * @param {String, Object, Array} params
     * @return []
     */
    handleParams(params) {
        return params;
    }

    menu(param) {
        const config = menu(param)
        if (config) {
            this.add(config)
        }
    }

    error(code) {
        const errCode = config.error(code);
        if (errCode) {
            this.add(errCode);
        }
    }

    tool(params) {
        g7s.common({
            menu: true
        })
    }

    add(userCommand) {
        const message = this.handleParams(userCommand);
        this.queue.push(...message);
        if (!runing) {
            this.run();
        }
    }

    sendMessage(message) {
        window.parent.postMessage(JSON.stringify(message), '*');
    }

    run() {
        if (runing) return;
        if (Array.isArray(this.queue)) {
            runing = true;
            const message = this.queue.shift();
            if (message) {
                this.sendMessage(message);
                runing = false;
                this.run();
            }
        }
    }
}
