
import Request from '@ued/request';
import qs from 'qs';
import { makeApiSign, getEnvInfo } from './util';
import G7s from './g7s';

const g7s = new G7s();

const ErrorCode = {
    UN_LOGIN_CODE: 196,
    UCENTER_ERROR_CODE: 23,
};

/**
 * vega: {
 *      test: {
 *          accessId: '',
 *          secretKey: '',
 *      },
 *      demo: {
 *          accessId: '',
 *          secretKey: '',
 *      },
 *      online: {
 *          accessId: '',
 *          secretKey: '',
 *      }
 *  },
 * apis: {} // 相关的api，
 * Vue // vue实例
 */
export default class VegaHttp {
    constructor(config) {
        const { apis, vega, Vue } = config;
        this.vm = Vue;
        this.apis = apis;
        this.vega = vega;
    }

    envUrl() {
        return {
            online: '//vega.huoyunren.com/',
            demo: '//demo.dsp.chinawayltd.com/vega-gw/rest/',
            test: '//172.22.34.203:5555/',
            local: 'localhost'
        }
    }

    getApi() {
        const env = this.environment(this.vega);
        const request = this.request(env);
        return request.createApi({ ...this.apis });
    }

    // 获取环境信息
    environment(vega) {
        const envInfo = getEnvInfo();
        const envUrl = this.envUrl();
        const env = {};

        for (const [key, value] of Object.entries(envUrl)) {
            const item = vega[key] || {};
            env[key] = {
                url: value,
                accessId: item.accessId || '',
                secretKey: item.secretKey || '',
            }
        }
        return env[envInfo.env] || env['online']
    }

    request(env) {
        return new Request({
            baseURL: env.url,
            timeout: 15000,
            transformRequest: [data => qs.stringify(data)],
        }, this.interceptors());
    }

    interceptors() {
        return {
            request: this.interceptorsRequest(),
            response: this.interceptorsResponse(),
        };
    }

    notify(message, delay) {
        this.vm.$message.error(message, Math.ceil(delay / 1000));
    }

    interceptorsRequest() {
        return [
            (config) => {
                const path = config.url.match(/\/v\d+(\S+(\/\S+\/\S+))$/)[2];
                if (!path) {
                    console.error('请求参数解析失败');
                }
                const { accessId, secretKey } = this.environment(this.vega);
                const sign = makeApiSign({
                    reqType: config.method,
                    path,
                    version: 'v1',
                    accessId,
                    secretKey,
                });

                if (config.method === 'get') {
                    config.params = Object.assign(sign, config.params);
                } else {
                    config.data = Object.assign(sign, config.data);
                }
                return config;
            }, error => Promise.reject(error)
        ];
    }

    interceptorsResponse() {
        return [
            (response) => {
                const data = response.data;
                if (data.code !== 0) {
                    if (data.code === ErrorCode.UN_LOGIN_CODE || data.code === ErrorCode.UCENTER_ERROR_CODE) {
                        g7s.logout();
                    }
                    this.notify(response.data.msg, 5000)
                    return Promise.reject(response);
                }
                const code = data.sub_code * 1;
                if (code !== 0) {
                    const message = response.data.sub_msg;
                    if (message) this.notify(message, 8000);
                    return Promise.reject(response);
                }
                return data.data;
            }, (error) => {
                return Promise.reject(error);
            },
        ]
    }
}